}
#gradientColor {
    display: flex;
    align-self: flex-end;
    width: 100%;
    height: 75vh;
    background: rgb(0, 0, 0);
    background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 1) 15%,
        rgba(0, 0, 0, 0) 100%
    );
}
#box-h-text {
    width: 50%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
