    color: white;
    text-align: center;
}
#box-h-text > h1 {
    font-size: 8vw;
}
#box-h-text > p {
    font-size: 2.5vw;
}

/*Card*/
#boxCard {
    width: 100%;
    height: 60vh;
    display: flex;
    justify-content: center;
    background-color: black;
}
#boxCard-C-G {
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}
#boxCardContent {
    overflow-y: scroll;
    width: 100%;
    padding: 5vh 1vh 5vh 1vh;
    display: flex;
    flex-direction: column;
}
#boxCardContent::-webkit-scrollbar {
    display: none;
}
#boxBardContent {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

#gradientColor-U {
    z-index: 1;
    height: 5vh;
    position: absolute;
    width: 100%;
    background: rgb(0, 0, 0);
    background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 1) 15%,
        rgba(0, 0, 0, 0) 100%
    );
}
#gradientColor-D {
    z-index: 1;
    width: 100%;
    position: absolute;
    bottom: 0;
    height: 5vh;
    background: rgb(0, 0, 0);
    background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 1) 15%,
        rgba(0, 0, 0, 0) 100%
    );
}
#viewMore{
    width: 50%;
    height: 5vh;
    position: absolute;
    color: white;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    transition: 1s;
}
.boxView{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

#triangleMore{
    width: 2vw;
    height: 2vw;
    border-color: #fff;
    transform: rotate(225deg);
}
.shapes {
    position: absolute;
    border: solid black;
    border-width: 0.5vw 0 0 0.5vw;
    z-index: 1;
    transition: 0.5s;
}
#endingLine {
    background-color: black;
    width: 100%;
    height: 20vh;
}

